

import React from "react";
import { NavLink } from "react-router-dom";
import s from './Nav.module.css';
import Picon from './img/profile-icon.png'
import Dicon from './img/dialogs-icon.png'
import Ficon from './img/friends-icon.png'

type NavElPorpsType={
  src:string,
  name:string
}


const NavEl:React.FC<NavElPorpsType> = (props) => {
  return (
    <div className={s.navImg}>
    <img src={props.src} alt="Profile icon"></img>
      <NavLink to={"/"+props.name}>{props.name}</NavLink>
    </div>
  )
}



const Nav = () => {
  let navData=[
    {src:Picon, name:"Profile",id:"1" },
    {src:Dicon, name:"Dialogs",id:"2" },
  
    {src:Ficon, name:"Friends",id:"6" }
  ]
  let NavComponent=navData.map((n)=>(<NavEl src={n.src} name={n.name} key={n.id}/>))
  return (
    <div className={s.nav}>
      <div className={s.navWrapper}>
      {NavComponent}
    </div>
    </div>  
  );
};

export default Nav;
